@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  margin: 0;
  min-height: 100%;
  background-color: #e7e5e5;
  font-family: "PT Sans", sans-serif;
}

.site-wrapper {
  max-width: 1200px;
  min-width: 320px;
  margin: 0 auto;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.main-header {
  padding: 17px 18px;
  min-height: 56px;
  display: flex;
  background-color: #ffffff;
  align-items: center;
  position: relative;
}

.logo {
  height: 96px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.main-nav {
  margin-left: auto;
}
@media (min-width: 768px) {
  .main-nav {
    margin: 0;
    width: 100%;
  }
}

.main-nav__list {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: block;
    position: static;
  }
}

.main-nav__control-input {
  margin: 0;
}

.main-nav__control {
  display: flex;
  align-items: center;
  width: 22px;
  height: 22px;
}
@media (min-width: 768px) {
  .main-nav__control {
    display: none;
  }
}

.main-nav__burger {
  display: block;
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
  position: relative;
  z-index: 2;
}
.main-nav__burger::before, .main-nav__burger::after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
  position: absolute;
  left: 0;
}
.main-nav__burger::before {
  top: -9px;
}
.main-nav__burger::after {
  bottom: -9px;
}
.main-nav__burger:hover {
  opacity: 0.6;
}
.main-nav__burger:active {
  opacity: 0.3;
}

.main-nav__list {
  transition: all 0.5s ease-in-out;
}

#burger:checked + label + ul {
  display: block;
}

#burger:checked + label span {
  background-color: #ffffff;
  transform: rotate(45deg);
}

#burger:checked + label span::before {
  background-color: #ffffff;
  top: 0;
  left: 0;
  transform: rotate(90deg);
}

#burger:checked + label span::after {
  display: none;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #81b3d2;
  text-align: center;
}
@media (min-width: 768px) {
  .site-list {
    background: none;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}

.site-list__item {
  padding: 17px 0 12px;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .site-list__item {
    padding: 0;
    border: none;
  }
  .site-list__item:nth-child(3) {
    grid-column: 4/5;
  }
}

.site-list__item:last-child {
  border-bottom: none;
  box-shadow: inset 0px -10px 15px -10px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .site-list__item:last-child {
    box-shadow: none;
  }
}

.site-list__item:first {
  border-top: none;
}

.site-list__link {
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
}
.site-list__link:hover {
  color: #766357;
}
@media (min-width: 768px) {
  .site-list__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}